<template>
  <div class="wrap-g">
    <!--轮播i图-->
    <div class="bgfff" style="padding: 15px;">
      <el-carousel :interval="0" arrow="always" height="240px"  indicator-position="none" style=";border-radius: 15px;background: #fff;box-shadow:rgba(136, 136, 136, 0.42) 0px 0px 10px">
        <el-carousel-item v-for="item in 1" :key="item">
          <div class="item">
            <img src="../../../static/img/home/mainimg.png" style="width: 100%;height: 200px">
            <div style="line-height: 40px;text-align: center;color: #1B1B1B;font-family:PingFang-SC-Medium;font-size: 18px;background: #fff;font-weight: bold">郑博文-商魂</div>
          </div>
        </el-carousel-item>
      </el-carousel>

    </div>

    <div class="flex nav bgfff">
      <div class="flex1 active">最新</div>
      <div class="flex1">最热</div>
      <div class="flex1">A-Z</div>
    </div>

    <!--推荐老师-->
    <div class="main1 bgfff" v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
      <div class="Teacher"  v-for="(item,index) in list" :key="index" style="padding: 10px 0;border-bottom: 1px solid #f2f2f2">
        <img src="../../../static/img/home/Advertisement.png" class="user">
        <div style="margin-left:150px;position: relative;top: 10px;">
          <div class="name">老师名字</div>
          <div class="msg1">
            <p>来历XXXX</p>
            <p>XXX课程 | 123123位学院</p>
          </div>
        </div>
        <div class="msg2">推荐学习：1232131232133</div>



      </div>
    </div>

    <!--推荐老师加载更多-->
    <div class="pageLoad" v-if="pageLoad">
      <i class="el-icon-loading"></i>
      上划自动加载
    </div>
  </div>

</template>

<script>
  export default {
    data () {
      return {
        activeName:"first",
        input5:"",
        list:[1,2,3,4],
        pageLoad:false,


        Recommendcourses:{    //推荐课程
          list:[],
          load:false
        }

      }
    },
    methods:{
      handleClick(e){
      },
      loadMore() {
        this.pageLoad = true;
        setTimeout(() => {
          let last = this.list[this.list.length - 1];
          for (let i = 1; i <= 2; i++) {
            this.list.push(last + i);
          }
          this.pageLoad = false;
        }, 2500);
      },

    },
    mounted(){

    },
  }
</script>


<style lang="less" scoped>
  .nav .flex1{
    background: #FFF;
    border: 1px solid #BFBFBF;
    color: #1B1B1B;
    text-align: center;
    padding:10px  5px;
    border-radius: 10px;
    font-size: 12px;

    margin: 5px 15px;
  }
  .nav .flex1.active{
    color: #F39800;
    border-color: #F39800 ;
  }
</style>



